import zyItem from "./zy-item.vue"
export default {
	props: {
		currentList: {
			type: Array
		}
	},
	computed: {
		songList() {
			let list = []
			for (let i = 0; i < this.currentList.length; i += 3) {
				let endVal = i + 3;
				if (endVal > this.currentList.length) {
					endVal = this.currentList.length
				}
				list.push(this.currentList.slice(i, endVal))
			}
			return list
		}
	},
	components: {
		zyItem
	}
}
